<template>
    <div v-if="uploading" class="upload-mask">
        <div class="upload-mask-content">
            <n-spin size="large" />
            <div class="upload-text">Ê®°ÂûãÁîüÊàê‰∏≠ÔºåËØ∑Á®çÂÄô...</div>
        </div>
    </div>
    <div class="image-uploader-panel">
        <n-card
            title="ÂèÇËÄÉÂõæÁâáÁîü‰∫ß3D"
            size="small"
            class="panel-card"
            :header-style="{
                textAlign: 'center',
            }"
            :style="{
                backgroundColor: '#1a1a1a',
                color: '#ffffff',
                border: '1px solid #444',
            }"
        >
            <div style="margin-bottom: 6px; padding-bottom: 6px; border-bottom: 2px solid #5f5f5f; word-break: break-all">
                <span>Ê∑∑ÂÖÉ3DÊúçÂä°Ôºö</span><br />
                <span>{{ urlConfig ? urlConfig : 'Êú™ÈÖçÁΩÆ' }}</span>
                <n-space justify="end">
                    <n-button type="primary" size="tiny" @click="setupConfig"> ËÆæÁΩÆ </n-button>
                    <n-button type="info" size="tiny" style="margin-right: 6px" @click="gotourl"> ËØ¥Êòé </n-button>
                </n-space>
            </div>
            <div v-if="!uploading && !uploaded">
                <n-upload :default-upload="false" :show-file-list="false" accept="image/*" @change="handleFileChange">
                    <n-button type="primary" size="small">ÈÄâÊã©ÂõæÁâá</n-button>
                </n-upload>
            </div>

            <div v-if="selectedFile && !uploaded">
                <n-image :src="previewUrl" width="100" height="100" object-fit="cover" style="margin-top: 10px" />
                <div style="margin-top: 10px">
                    <n-button type="success" size="small" :loading="uploading" @click="submitImage"> Êèê‰∫§ </n-button>
                </div>
            </div>

            <div v-if="uploaded">
              <span>ÂèÇËÄÉÂõæÁâáÔºö</span><br />
                <n-image :src="previewUrl" width="100" height="100" object-fit="cover" style="margin-top: 10px" />
                <n-space>
                <div style="margin-top: 10px">
                    <n-button type="info" size="small" @click="removeImage">ÈáçÊñ∞Áîü‰∫ß</n-button>
                </div>
                <div style="margin-left: 30px; margin-top: 10px">
                    <n-button type="success" size="small" @click="downloadModel">‰∏ãËΩΩÊ®°Âûã</n-button>
                </div>
                </n-space>
            </div>
        </n-card>
    </div>
</template>

<script setup>
import { ref, h } from 'vue'
import { NCard, NButton, NUpload, useDialog, useMessage, NImage, NSpin, NSpace, NInput, NAlert } from 'naive-ui'
import { useMaterialDiyStore } from 'PLS/hunyuan3D/stores/index'
import { loadArrayBufferFromRelativePath } from 'PLS/hunyuan3D/common/util'
import { download } from 'naive-ui/es/_utils'

const materialDiyStore = useMaterialDiyStore()
materialDiyStore.curModelArrayBuffer = await loadArrayBufferFromRelativePath('./plugins/hunyuan3D/model/icegl-bm.glb') // icegl-bm

const message = useMessage()

const selectedFile = ref(null)
const previewUrl = ref('https://cdn.index.icegl.cn/uploads/20221122/9ed6756107c4e0c031ae26d54cb46c0d.png')
const uploaded = ref(true)
const uploading = ref(false)
const dialog = useDialog()
const urlConfig = ref('http://localhost:8081/generate')

function handleFileChange({ file }) {
    selectedFile.value = file.file
    previewUrl.value = URL.createObjectURL(file.file)
}
function toBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = () => {
            const base64 = reader.result.split(',')[1] // ÂéªÊéâÂâçÁºÄ data:image/png;base64,
            resolve(base64)
        }
        reader.onerror = reject
    })
}
async function submitImage() {
    if (!selectedFile.value) return

    uploading.value = true

    try {
        const base64 = await toBase64(selectedFile.value)

        const requestData = {
            image: base64,
        }

        const response = await fetch(urlConfig.value, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(requestData),
        })

        materialDiyStore.curModelArrayBuffer = await response.arrayBuffer()

        // üëâ ËøôÈáå‰Ω†ÂèØ‰ª•‰ΩøÁî® Blob È¢ÑËßàÊàñ‰∏ãËΩΩ
        // const blob = new Blob([arrayBuffer], { type: 'model/gltf-binary' })
        // const blobUrl = URL.createObjectURL(blob)
        // const a = document.createElement('a')
        // a.href = blobUrl
        // a.download = 'generated-model.glb'
        // a.click()
        // URL.revokeObjectURL(blobUrl)

        uploaded.value = true
        message.success('‰º†ÈÄíÊé•Âè£ÊàêÂäüÔºåÊ®°ÂûãÂ∑≤ÁîüÊàê')
    } catch (error) {
        console.error(error)
        message.error('‰º†ÈÄíÊé•Âè£Â§±Ë¥•')
    } finally {
        uploading.value = false
    }
}

function downloadModel() {
    if (!materialDiyStore.curModelArrayBuffer) {
        message.error('Ê≤°ÊúâÂèØ‰∏ãËΩΩÁöÑÊ®°Âûã')
        return
    }

    const blob = new Blob([materialDiyStore.curModelArrayBuffer], { type: 'model/gltf-binary' })
    const blobUrl = URL.createObjectURL(blob)
    download(blobUrl, 'generated-model.glb')
    URL.revokeObjectURL(blobUrl)
}

function removeImage() {
    selectedFile.value = null
    previewUrl.value = ''
  uploaded.value = false
    materialDiyStore.curModelArrayBuffer = null
}
const setupConfig = () => {
    const error = ref('')
    const tempurl = ref(urlConfig.value)
    dialog.create({
        title: 'ËØ∑ËæìÂÖ•Ê∑∑ÂÖÉ3DÊúçÂä°Âú∞ÂùÄ:',
        content: () =>
            h('div', {}, [
                h(NInput, {
                    value: tempurl.value,
                    placeholder: "ÊúçÂä°ËØ¥ÊòéËØ¶ËßÅ'ËØ¥Êòé'ÊåâÈíÆ",
                    onInput: (val) => {
                        tempurl.value = val
                        error.value = ''
                    },
                }),
                error.value &&
                    h(
                        NAlert,
                        {
                            type: 'error',
                            style: 'margin-top: 8px',
                        },
                        {
                            default: () => error.value,
                        },
                    ),
            ]),
        positiveText: 'Á°ÆËÆ§',
        negativeText: 'ÂèñÊ∂à',
        maskClosable: false,
        onPositiveClick: () => {
            if (!tempurl.value.trim()) {
                error.value = 'ÈÖçÁΩÆ‰∏çËÉΩ‰∏∫Á©∫'
                return false
            }
            urlConfig.value = tempurl.value.trim()
            return true
        },
    })
}
const gotourl = () => {
    window.open('https://icegl.cn/ask/article/22765.html', '_blank')
}
</script>
