import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader'

async function loadArrayBufferFromRelativePath(relativePath) {
    try {
        const response = await fetch(relativePath)
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`)
        }
        const arrayBuffer = await response.arrayBuffer()
        return arrayBuffer
    } catch (err) {
        console.error('ËØªÂèñÂ§±Ë¥•:', err)
        return null
    }
}

/**
 * ÂºÇÊ≠•Âä†ËΩΩÊîØÊåÅ Draco ÁöÑ glb buffer Âπ∂ËøîÂõû gltf ÁªìÊûÑ
 * @param {ArrayBuffer} arrayBuffer - ‰∫åËøõÂà∂Ê®°ÂûãÊï∞ÊçÆ
 * @returns {Promise<THREE.Group>} - ËøîÂõû ÔºàÊ®°ÂûãÔºâ
 */
async function loadGLTFBufferIntoScene(arrayBuffer) {
    const loader = new GLTFLoader()
    const dracoLoader = new DRACOLoader()
    dracoLoader.setDecoderPath('./draco/')
    loader.setDRACOLoader(dracoLoader)

    return new Promise((resolve, reject) => {
        loader.parse(
            arrayBuffer,
            '',
            (gltf) => {
                resolve(gltf)
            },
            (error) => {
                console.error('GLB Âä†ËΩΩÂ§±Ë¥•:', error)
                reject(error)
            },
        )
    })
}

export { loadArrayBufferFromRelativePath, loadGLTFBufferIntoScene }
